import React, { useRef, useEffect } from 'react';
import TranslatableField from '../shared/TranslatableField';

function TinyMCEField({ 
  label, 
  value, 
  onChange, 
  help, 
  i18n = false,
  required = false,
  error,
  height = 400,
  toolbar = 'formatselect | bold italic underline strikethrough | alignleft aligncenter alignright | bullist numlist | link unlink | code',
  plugins = 'link lists code'
}) {
  const editorRef = useRef(null);
  const textareaRef = useRef(null);

  useEffect(() => {
    // Check if TinyMCE is available
    if (typeof window.tinymce === 'undefined') {
      console.warn('TinyMCE is not loaded. Using fallback textarea.');
      return;
    }

    // Initialize TinyMCE
    window.tinymce.init({
      target: textareaRef.current,
      height: height,
      menubar: false,
      plugins: plugins,
      toolbar: toolbar,
      content_style: 'body { font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif; font-size: 14px; }',
      setup: (editor) => {
        editorRef.current = editor;
        
        editor.on('change', () => {
          const content = editor.getContent();
          onChange(content);
        });
        
        editor.on('init', () => {
          editor.setContent(value || '');
        });
      }
    });

    return () => {
      if (editorRef.current) {
        window.tinymce.remove(editorRef.current);
      }
    };
  }, []);

  useEffect(() => {
    // Update editor content when value changes externally
    if (editorRef.current && editorRef.current.getContent() !== value) {
      editorRef.current.setContent(value || '');
    }
  }, [value]);

  const editorElement = (
    <div className="ev2-border ev2-border-gray-300 ev2-rounded-md ev2-overflow-hidden">
      <textarea
        ref={textareaRef}
        value={value || ''}
        onChange={(e) => onChange(e.target.value)}
        className="ev2-w-full ev2-p-3"
        style={{ minHeight: height }}
      />
    </div>
  );

  if (i18n) {
    return (
      <TranslatableField
        label={label}
        value={value}
        onChange={onChange}
        required={required}
        error={error}
        help={help}
      >
        {editorElement}
      </TranslatableField>
    );
  }

  return (
    <div className="ev2-space-y-1">
      {label && (
        <label className="ev2-flex ev2-items-center ev2-gap-2 ev2-text-sm ev2-font-medium ev2-text-gray-700">
          <span>{label}</span>
          {required && <span className="ev2-text-red-500">*</span>}
        </label>
      )}
      {editorElement}
      {help && (
        <p className="ev2-text-xs ev2-text-gray-500">{help}</p>
      )}
      {error && (
        <p className="ev2-text-xs ev2-text-red-600">{error}</p>
      )}
    </div>
  );
}

export default TinyMCEField;